import React, { useEffect, useState } from 'react'
import { Input, Button, message, Select, Checkbox, Modal } from 'antd'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import { signupAction } from '../../../redux/userDuck'
import text from '../../text'
import { changeLangAction } from '../../../redux/appDuck'
import { MaskedInput } from 'antd-mask-input'

let { Option } = Select

let AgentData = ({fetching, language, error, info, signup, history, changeLanguage}) => {

    let [data, setData] = useState({
        userType: 'agent'
    })

    let showTerms = () => {
        Modal.info({
            content: (
                <div className='info-div'>
                    <h1>Terms of Service</h1>
                    <p>These HTML-CSS-JS Analytics Terms of Service (this "Agreement") are entered into by Ruwix Services SRL. ("WWWEEEBBB") and the entity executing this Agreement ("You"). This Agreement governs Your use of this website (the "Service"). BY STAYING ON THIS WEBSITE, AND USING THE SERVICE, YOU ACKNOWLEDGE THAT YOU HAVE REVIEWED AND ACCEPT THIS AGREEMENT AND ARE AUTHORIZED TO ACT ON BEHALF OF, AND BIND TO THIS AGREEMENT. In consideration of the foregoing, the parties agree as specified below.</p>
                    <p>We may amend this Agreement at any time by posting the amended terms on our Website. We may or may not post notices on the homepage of our Website when such changes occur.</p>
                    <p>We refer to this Agreement, our Privacy Policy accessible below, and any other terms, rules, or guidelines on our Website collectively as our "Legal Terms." You explicitly and implicitly agree to be bound by our Legal Terms each time you access our Website. If you do not wish to be so bound, please do not use or access our Website.</p>
                    <h2>Limited License</h2>
                    <p>HTML CSS JS grants you a non-exclusive, non-transferable, revocable license to access and use our Website in order for you to make purchases of electronic documents and related services through our Website, strictly in accordance with our Legal Terms.</p>
                    <h2>Copyrights and Trademarks</h2>
                    <p>Unless otherwise stated, all materials including without limitation, logos, brand names, images, designs, photographs, video clips, written and other materials that appear as part of our Website are copyrights, trademarks, service marks, trade dress and/or other intellectual property whether registered or unregistered ("Intellectual Property") owned, controlled or licensed by HTML CSS JS. Our Website as a whole is protected by copyright and trade dress. Nothing on our Website should be construed as granting, by implication, estoppel or otherwise, any license or right to use any Intellectual Property displayed or used on our Website, without the prior written permission of the Intellectual Property owner. HTML CSS JavaScript aggressively enforces its intellectual property rights to the fullest extent of the law. The names and logos of HTML CSS JS, may not be used in any way, including in advertising or publicity pertaining to distribution of materials on our Website, without prior, written permission from HTML CSS Javascript. Fair use of HTML CSS JS&rsquo;s Intellectual Property requires proper acknowledgment. Other product and company names mentioned in our Website may be the Intellectual Property of their respective owners.</p>
                    <h2>Links to Third-Party Websites</h2>
                    <p>Our Website contains links to Websites owned or operated by parties other than HTML CSS JS. Such links are provided for your reference only. HTML CSS JS does not monitor or control outside Websites and is not responsible for their content. HTML CSS JS&rsquo;s inclusion of links to an outside Website does not imply any endorsement of the material on our Website or, unless expressly disclosed otherwise, any sponsorship, affiliation or association with its owner, operator or sponsor, nor does HTML CSS JS&rsquo; inclusion of the links imply that HTML CSS JS is authorized to use any trade name, trademark, logo, legal or official seal, or copyrighted symbol that may be reflected in the linked Website.</p>
                    <h2>Content Disclaimer</h2>
                    <p>Postings on our Website are made at such times as HTML CSS JS determines in its discretion. You should not assume that the information contained on our Website has been updated or otherwise contains current information. HTML CSS JS does not review past postings to determine whether they remain accurate and information contained in such postings may have been superseded. THE INFORMATION AND MATERIALS IN OUR WEBSITE ARE PROVIDED FOR YOUR REVIEW IN ACCORDANCE WITH THE NOTICES, TERMS AND CONDITIONS SET FORTH HEREIN. THESE MATERIALS ARE NOT GUARANTEED OR REPRESENTED TO BE COMPLETE, CORRECT OR UP TO DATE. THESE MATERIALS MAY BE CHANGED FROM TIME TO TIME WITHOUT NOTICE.</p>
                    <h2>No Warranties; Exclusion of Liability; Indemnification</h2>
                    <p>our website is operated by HTML CSS JS on an "as is," "as available" basis, without representations or warranties of any kind. To the fullest extent permitted by law, HTML CSS JS specifically disclaims all warranties and conditions of any kind, including all implied warranties and conditions of merchantability, fitness for a particular purpose, title and noninfringement for our website and any contracts and services you purchase through it. HTML CSS JS shall not have any liability or responsibility for any errors or omissions in the content of our website, for contracts or services sold through our website, for your action or inaction in connection with our website or for any damage to your computer or data or any other damage you may incur in connection with our website. Your use of our website and any contracts or services are at your own risk. In no event shall either HTML CSS JS or their agents be liable for any direct, indirect, punitive, incidental, special or consequential damages arising out of or in any way connected with the use of our website, contracts and services purchased through our website, the delay or inability to use our website or otherwise arising in connection with our website, contracts or related services, whether based on contract, tort, strict liability or otherwise, even if advised of the possibility of any such damages. In no event shall HTML CSS JS&rsquo; liability for any damage claim exceed the amount paid by you to HTML CSS JS for the transaction giving rise to such damage claim.</p>
                    <p>some jurisdictions do not allow the exclusion or limitation of incidental or consequential damages, so the above exclusion may not apply to you.</p>
                    <p>without limiting the foregoing, HTML CSS JS do not represent or warrant that the information on the webite is accurate, complete, reliable, useful, timely or current or that our website will operate without interruption or error.</p>
                    <p>you agree that all times, you will look to attorneys from whom you purchase services for any claims of any nature, including loss, damage, or warranty. HTML CSS JS and their respective affiliates make no representation or guarantees about any contracts and services offered through our website.</p>
                    <h2>Indemnification</h2>
                    <p>You agree to defend, indemnify and hold HTML CSS JS harmless from and against any and all claims, damages, costs and expenses, including attorneys' fees, arising from or related to your use of our Website or any Contracts or Services you purchase through it.</p>
                    <h1>Privacy Policy</h1>
                    <p><strong>We are not collecting personal data but we do use third party applications that may do. Please verify their privacy policies to find out more: Google Analytics, Google Adsense, Gmail, Facebook, Youtube. We might add or remove thrid party applications to the website at any time without notice. Please check the source code of the site or use browser plugins to identify them.</strong></p>
                    <p>Our Privacy Policy was posted on 13 September 2012 and last updated on 16 April 2018. It governs the privacy terms of our Website, located at html-css-js.com. Any capitalized terms not defined in our Privacy Policy, have the meaning as specified in our Terms of Use accessible above.</p>
                    <h2>Your Privacy</h2>
                    <p>HTML CSS JS follows all legal requirements to protect your privacy. Our Privacy Policy is a legal statement that explains how we may collect information from you, how we may share your information, and how you can limit our sharing of your information.</p>
                    <h2>Definitions</h2>
                    <p>"Non Personal Information" is information that is not personally identifiable to you and that we automatically collect when you access our Website with a web browser. It may also include publicly available information that is shared between you and others.</p>
                    <p>"Personally Identifiable Information" is non-public information that is personally identifiable to you and obtained in order for us to provide you within our Website. Personally Identifiable Information may include information such as your name, email address, and other related information that you provide to us or that we obtain about you.</p>
                    <h2>Information We Collect</h2>
                    <p>Generally, you control the amount and type of information you provide to us when using our Website.</p>
                    <p>As a Visitor, you can browse our website to find out more about our Website. You are not required to provide us with any Personally Identifiable Information as a Visitor.</p>
                    <p>When you use our Website, we automatically collect certain computer information by the interaction of your mobile phone or web browser with our Website. Such information is typically considered Non Personal Information.</p>
                    <h2 id="cookies">Cookies</h2>
                    <p>This website is using cookies to provide a better user experience, to collect anonymous visitor analytics and to show personalized ads. These are small browser files and you can disable and erase them in your browser settings or track them with Google Chorme's <em>Web Developer</em> plugin. We show warnings about the use of cookies to European visitors according to the GDPR law.</p>
                    <p>We use mostly third party cookies such as Google Analytics to track visitor activity, Google AdSense to show personalized ads. Other addons, such as TinyMCE, Codemirror or even our shared hosting provider can also use cookies. Please consult their privacy policies to learn more. The only first-party cookie we use is to track the consent of European visitors for our use of cookies.</p>
                    <h2>Advertising</h2>
                    <p>html-css-js.com might show AdSense ads.</p>
                    <h1>The HTML-CSS-JS.COM navigation bar</h1>
                    <p>The HTML-CSS-JS navigation bar located at the top of each page of the website is a collection of online web developer tools. We affiliate with other websites offering similar services to use the same <strong>HTML CSS JavaScript navigation menu</strong> on all member sites. If you have a free online tool which helps web developers and you would be happy to join our group using the navigation bar above then you are welcome to contact us on the email address in the footer.</p>
                    <p>Please stop using the website if you disagree with any of the terms below.</p>
                </div>
            ),
            width: '90%',
            className:'info-modal',
            okText: text.register.agreeTerms[language],
            onOk: () => signup(data)
        })
    }

    let handleData = e => {
        setData({...data, [e.target.name]: e.target.value})
    }

    let handleSubmit = () => {
        if(!data.name ||
             !data.lastName ||
             (!data.secondLastName && language === 'español') ||
             !data.company ||
             !data.email ||
             !data.phone
            ) 
            return message.warning(text.messages.missingFields[language])
        let rex = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        if(!rex.test(data.email.toLowerCase())) return message.warn(text.messages.incorrectEmail[language])
        if(!data.password || data.password.length < 6) return message.warn(text.messages.incorrectPassword[language])
        if(data.password !== data.repeatPassword) return message.warn(text.messages.repeatedPasswordError[language])
        if(!data.consent) return message.warn(text.messages.consent[language])
        showTerms()
    }

    useEffect(() => {
        if(error.status === 500) return message.error('Server error')
        if(info.name) return history.push('/profile')
    }, [error, info])

    return (
        <div className='form-container'>
            <div className="multiple-inputs">
                <Input onChange={handleData} value={data.name} name='name' placeholder={text.register.firstName[language]}/>
                <Input className={`${language === 'english' && 'last-input'}`} onChange={handleData} value={data.lastName} name='lastName' placeholder={text.register.lastName[language]}/>
                {language === 'español' && <Input required onChange={handleData} value={data.secondLastName} name='secondLastName' placeholder={text.register.secondLastName[language]}/>}
            </div>
            <Input onChange={handleData} value={data.company} name='company' placeholder={text.register.company[language]}/>
            <div className="multiple-inputs">
                <Input onChange={handleData} value={data.email} name='email' placeholder={text.register.email[language]}/>
                {
                    language === 'english' ?
                        <MaskedInput className='last-input' mask="1-111-1111111" name="phone" onChange={handleData} value={data.phone} placeholder={text.register.phone[language]}/>
                    :
                        <MaskedInput className='last-input' mask="11-1111-1111" name="phone" onChange={handleData} value={data.phone} placeholder={text.register.phone[language]}/>
                }
            </div>
            <div className="multiple-inputs">
                <Select className='agent-selector' onChange={value => {setData({...data, language: value}); changeLanguage(value)}} placeholder={text.register.language[language]} value={data.language} defaultValue={data.language}>
                    <Option value='english'>English</Option>
                    <Option value='español'>Español</Option>
                </Select>
            </div>
            <Input.Password onChange={handleData} value={data.password} name='password' placeholder={text.register.password[language]}/>
            <Input.Password onChange={handleData} value={data.repeatPassword} name='repeatPassword' placeholder={text.register.repeatPassword[language]}/>
            <br/><br/>
            <div className="checkbox-input">
                <Checkbox
                    checked={data.consent}
                    onChange={e => setData({...data, consent: e.target.checked})}
                >
                    {text.register.confirm[language]}
                </Checkbox>
            </div>
            <div className="checkbox-input">
                <Checkbox
                    checked={data.allowNewsletter}
                    onChange={e => setData({...data, allowNewsletter: e.target.checked})}
                >
                    {text.register.allowNewsletter[language]}
                </Checkbox>
            </div>
            <br/><br/>
            <Button loading={fetching} onClick={handleSubmit} className='nav-button'>{text.button.register[language]}</Button>
            <div className="auth-bottom-text">
                <p>{text.register.login[language]} <Link to='/login'>{text.register.loginLink[language]}</Link></p>
            </div>
        </div>
    )
}

let mapState = ({
    user: {fetching, error, info},
    app: {language}
}) => ({
    fetching,
    error, 
    info,
    language
})

export default connect(
    mapState,
    {
        signup: signupAction,
        changeLanguage: changeLangAction
    }
)(AgentData)